body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.private-header {
  width: 100%;
  padding-bottom: 20px;
  font-family: "Arial Black", "Arial", "Verdana";
}

.page-links {
  color: white;
  background-color: orange;
  height: 50px;
  float: right;
  width: 100%;
  position: fixed;
}

.page-links h5 {
  float: right;
  padding-right: 2%;

}

.header-content {
  padding-top: 2%;
  color: white;
  background-color: orange;
  height: 170px;
  text-align: center;
  padding-top: 50px;
}

.company-name {
  display: inline-block;
  margin: auto;
}

.search {
  background-color: white;
  width: 50%;
  min-width: 200px;
  display: inline-block;
}

.product-details {
  background-color: #fefefe;
  text-align: left;
  margin-left: 10%;
  border: 1px solid #888;
  width: 80%;
}

.close {
  color: #aaaaaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

.product-card {
  z-index: 1000;
}

.details {
  width: 100%;
}

.suggestion {
  border: 1px solid #888;
  text-align: center;
  height: 200px;
  padding-top: 20px;
  margin: 10px;
}

.product-list {
  background-color: #E8E8E8;
  font-family: "Verdana";

}

.products {
  width: 80%;
  margin-left: 10%;
  margin-right: 10%;
}

.product-list-img {
  text-align: center;
  margin-left: 10%;
}

Card {
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

#productDetails {
  display: none;
  position: fixed;
  z-index: 1;
  padding-top: 10px;
  padding-bottom: 10px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0,0.3);
}

