.private-header {
  width: 100%;
  padding-bottom: 20px;
  font-family: "Arial Black", "Arial", "Verdana";
}

.page-links {
  color: white;
  background-color: orange;
  height: 50px;
  float: right;
  width: 100%;
  position: fixed;
}

.page-links h5 {
  float: right;
  padding-right: 2%;

}

.header-content {
  padding-top: 2%;
  color: white;
  background-color: orange;
  height: 170px;
  text-align: center;
  padding-top: 50px;
}

.company-name {
  display: inline-block;
  margin: auto;
}

.search {
  background-color: white;
  width: 50%;
  min-width: 200px;
  display: inline-block;
}
